"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

export default function AuthPage() {
  const [isLogin, setIsLogin] = useState(true); // State สำหรับ Toggle Login/Signup

  return (
    <div className="flex justify-center items-center h-screen bg-gradient-to-br from-black to-blue-900">
      {/* Logo */}
      <div className="absolute top-6 left-6 text-white text-lg font-bold">
        <span className="text-white text-3xl">EQ</span>
      </div>

      {/* Toggle Button */}
      <div className="absolute top-6 right-6 flex gap-4">
        <button
          onClick={() => setIsLogin(!isLogin)}
          className="px-6 py-2 bg-blue-600 text-white rounded-full font-semibold shadow-lg transition-transform hover:scale-105"
        >
          {isLogin ? "Signup" : "Login"}
        </button>
      </div>

      {/* Auth Box with Animation */}
      <div className="relative">
        <AnimatePresence mode="wait">
          {isLogin ? (
            <motion.div
              key="login"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
              className="bg-gray-900 p-6 rounded-lg shadow-lg w-96"
            >
              <h2 className="text-2xl font-bold text-center text-white mb-4">Sign in</h2>
              <form className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-300">Email</label>
                  <input
                    type="email"
                    className="w-full px-4 py-2 border border-gray-700 rounded-md bg-gray-800 text-white focus:ring-2 focus:ring-blue-500"
                    placeholder="Enter your email"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-300">Password</label>
                  <input
                    type="password"
                    className="w-full px-4 py-2 border border-gray-700 rounded-md bg-gray-800 text-white focus:ring-2 focus:ring-blue-500"
                    placeholder="Enter your password"
                  />
                </div>
                <button
                  type="submit"
                  className="w-full bg-blue-600 text-white py-2 rounded-md hover:bg-blue-700 transition"
                >
                  Log in
                </button>
              </form>
            </motion.div>
          ) : (
            <motion.div
              key="signup"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
              className="bg-gray-900 p-6 rounded-lg shadow-lg w-96"
            >
              <h2 className="text-2xl font-bold text-center text-white mb-4">Sign up</h2>
              <form className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-300">Email</label>
                  <input
                    type="email"
                    className="w-full px-4 py-2 border border-gray-700 rounded-md bg-gray-800 text-white focus:ring-2 focus:ring-blue-500"
                    placeholder="Enter your email"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-300">Password</label>
                  <input
                    type="password"
                    className="w-full px-4 py-2 border border-gray-700 rounded-md bg-gray-800 text-white focus:ring-2 focus:ring-blue-500"
                    placeholder="Enter your password"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-300">Confirm Password</label>
                  <input
                    type="password"
                    className="w-full px-4 py-2 border border-gray-700 rounded-md bg-gray-800 text-white focus:ring-2 focus:ring-blue-500"
                    placeholder="Confirm your password"
                  />
                </div>
                <button
                  type="submit"
                  className="w-full bg-green-600 text-white py-2 rounded-md hover:bg-green-700 transition"
                >
                  Sign up
                </button>
              </form>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </div>
  );
}
